@font-face {
  font-family: 'Fira Code';
  src: url('FiraCode-Regular.otf') format('opentype');
}

html,
body,
#root,
#app>div {
  height: 100%;
  width: 100%;
  margin: 0;
}

* {
  box-sizing: border-box;
}

:root {
  --background-color: #282a36;
  --send-button-color: #ff5555;
  --try-again-button-color: #50fa7b;
  --grey: #525356;
  --white: #f8f8f2;
  --blue: #8BE9FD;
  background-color: var(--background-color);
  height: 100%;
  margin: 0;
  display: flex;
}

.page {
  font-family: 'Fira Code';
  background-color: var(--background-color);
  display: grid;
  grid-template-columns: 20% auto 20%;
  grid-template-rows: 10% auto 5%;
  width: 100%;
  height: 100%;
  vertical-align: middle;
}

.review-input-button {
  background-color: var(--send-button-color);
  font-size: 1.3em;
  text-align: center;
  border-radius: 6px;
  border: none;
  margin: 2%;
  grid-row: 2;
  width: fit-content;
  padding: 1%;
  font-family: 'Fira Code';
  transition: box-shadow 0.2s ease-in-out, font-size 0.2s;
  color: var(--white);
}

.review-input-button:hover {
  box-shadow: 0 0 1em var(--grey);
  font-size: 1.5em;
}


.review-input-button:focus {
  box-shadow: 0 0 1em var(--grey);
  outline: none;
}

.review-input-box {
  grid-row: 1;
  background-color: var(--grey);
  font-size: 30;
  width: 60%;
  vertical-align: middle;
  border-radius: 1em;
  padding: 2%;
  resize: none;
  border: none;
  text-align: center;
  transition: box-shadow 0.2s ease-in-out;
  align-self: center;
  font-family: 'Fira Code';
}

.review-input-box:focus {
  border: none;
  box-shadow: 0 0 0.3em var(--blue);
  outline: none;
}

.review-box {
  margin-top: 5%;
  display: grid;
  grid-template-rows: auto auto;
  align-content: center;
  grid-row: 3;
  grid-column: 2;
  justify-items: center;
}

.title {
  font-size: 2em;
  text-align: center;
  color: var(--white);
  grid-row: 2;
  grid-column: 2;
}

.logo {
  justify-self: right;
  grid-column: 3;
  width: 4em;
  margin-right: 0.3em;
  margin-top: 0.3em;
}

.group {
  display: flex;
  grid-row: 2;
  grid-column: 2;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.resultScreen {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  grid-row: 2 / 2;
  grid-column: 2 / 2;
  justify-items: center;
  align-items: center;
}

.result {
  grid-column-start: 1;
  grid-column-end: 1;
  display: flex;
  flex-direction: column;
  grid-template-rows: 60% auto;
  justify-items: center;
  width: 100%;
  padding: 10%;
  height: 100%;
}

.result-image {
  width: auto;
  size: auto;
}

.try-again-button {
  grid-column: 2;
  padding: 10%;
  background-color: var(--try-again-button-color);
  height: fit-content;
  font-size: 2em;
  border: none;
  border-radius: 1em;
  font-family: inherit;
  transition: box-shadow 0.2s ease-in-out;
  width: fit-content;
}

.try-again-button:hover {
  border: none;
  box-shadow: 0 0 0.3em var(--blue);
  outline: none;
}


.footer {
  grid-row: 3;
  grid-column: 2 / 2;
  display: flex;
  height: inherit;
  flex-grow: 1;
  align-content: center;
  width: 100%;
  margin-left: 2em;
  text-align: center;
  margin-bottom: 0.5em;
  align-items: center;
}

.footer>a {
  font-family: inherit;
  color: black;
  font-style: normal;
  text-decoration: none;
  height: inherit;
  text-align: center;
  display: flex;
  margin-left: 1em;
}

.footer>a>img {
  margin-right: 0.3em;
  height: fit-content;
  height: inherit;
  width: auto;
}

.img-bottom {
  background-image: url(img/jesus.svg);
  height: 40%;
}

.img-top {
  background-image: url(img/Subtract.svg);
  height: 110%;
}

.person {
  text-align: center;
  font-size: 2em;
  color: var(--white);
}

.description {
  text-align: center;
  color: var(--white);
  background-color: var(--grey);
}